// MessageBox.js

import React, {Component} from 'react';
import trim from 'trim';
import firebase from "firebase"

class MessageBox extends Component {

  constructor(props){
    super(props);
    this.onChange = this.onChange.bind(this);
    this.onKeyup = this.onKeyup.bind(this);
    this.state = {
      message: ''
    };
  }

  onChange(e){
    this.setState({
      message: e.target.value
    });
  }
  onKeyup(e){
    if(e.keyCode === 13 && trim(e.target.value) !== ''){
      e.preventDefault();
      let dbCon = firebase.database().ref('/messages');
      dbCon.push({
        message: trim(e.target.value),
        name: "Somchai Jaidee",
        profile: "https://scontent.fbkk9-2.fna.fbcdn.net/v/t39.30808-6/330798625_1149832079032681_8162569886586545197_n.jpg?_nc_cat=109&ccb=1-7&_nc_sid=09cbfe&_nc_eui2=AeEaB40xazWy9O0zYBnwPfdF_6fPk7IN8if_p8-Tsg3yJ-LSQVgm3bkDDMO0masEHipF6ecZzvNyJ9NzutdHNAmB&_nc_ohc=TjGdYwIDM0EAX9hYJh7&_nc_ht=scontent.fbkk9-2.fna&oh=00_AfAsfWpXZDyttvNGafCZTnL3_CChuWiPczu26sCgIK8OCQ&oe=64076D81"
      });
      this.setState({
        message: ''
      });
    }
  }

  render() {
        return (
          <form>
            <textarea
                className="textarea"
                placeholder="Comment"
                cols="100"
                onChange={this.onChange}
                onKeyUp={this.onKeyup}
                value={this.state.message}>
            </textarea>
          </form>
        )
  }
}

export default MessageBox